import React from "react";
import PropTypes from "prop-types";
import * as CONST from "../js/config.js";

function Slide12({ demo, language }) {
    if (language === "en") {
        return (
            <>
                <section className="slide-12" id={demo ? "" : "slide12"}>
                    <div className="slide12-container">
                        <div className="slide12-content">
                            <div className="slide12-text">
                                <h2>
                                    A few words about myself and let's get
                                    started!
                                </h2>
                                <p>
                                    My name is Sergei Morozov, and I have gained
                                    many years of experience working in
                                    investment companies in various positions.
                                    Today I know the inner workings of the
                                    brokerage business to help you make money in
                                    the market.
                                </p>
                                <div className="buttons">
                                    <a
                                        href={CONST.TG_ADMIN}
                                        target="_blank"
                                        rel="noreferrer"
                                    >
                                        <button className="button">
                                            Administrator Igor
                                        </button>
                                    </a>
                                    <a
                                        href={CONST.TG_CHANNEL}
                                        target="_blank"
                                        rel="noreferrer"
                                    >
                                        <button className="button">
                                            Telegram
                                        </button>
                                    </a>
                                    <a
                                        href={CONST.YT_CHANNEL}
                                        target="_blank"
                                        rel="noreferrer"
                                    >
                                        <button className="button">
                                            YouTube
                                        </button>
                                    </a>{" "}
                                    <a
                                        href={"https://t.me/Nasdaq_Gad_bot"}
                                        target="_blank"
                                        rel="noreferrer"
                                    >
                                        <button className="button">
                                            Telegram Bot
                                        </button>
                                    </a>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </>
        );
    }

    if (language === "ua") {
        return (
            <>
                <section className="slide-12" id={demo ? "" : "slide12"}>
                    <div className="slide12-container">
                        <div className="slide12-content">
                            <div className="slide12-text">
                                <h2>Кілька слів про себе і почнемо!</h2>
                                <p>
                                    Мене звати Сергій Морозов, і я здобув
                                    багаторічний досвід роботи в інвестиційних
                                    компаніях на різних посадах. Сьогодні я
                                    розумію внутрішню роботу брокерського
                                    бізнесу, щоб допомогти вам заробляти гроші
                                    на ринку.
                                </p>
                                <div className="buttons">
                                    <a
                                        href={CONST.TG_ADMIN}
                                        target="_blank"
                                        rel="noreferrer"
                                    >
                                        <button className="button">
                                            Адміністратор Ігор
                                        </button>
                                    </a>
                                    <a
                                        href={CONST.TG_CHANNEL}
                                        target="_blank"
                                        rel="noreferrer"
                                    >
                                        <button className="button">
                                            Telegram
                                        </button>
                                    </a>
                                    <a
                                        href={CONST.YT_CHANNEL}
                                        target="_blank"
                                        rel="noreferrer"
                                    >
                                        <button className="button">
                                            YouTube
                                        </button>
                                    </a>{" "}
                                    <a
                                        href={"https://t.me/Nasdaq_Gad_bot"}
                                        target="_blank"
                                        rel="noreferrer"
                                    >
                                        <button className="button">
                                            Telegram Bot
                                        </button>
                                    </a>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </>
        );
    }

    return (
        <>
            <section className="slide-12" id={demo ? "" : "slide12"}>
                <div className="slide12-container">
                    <div className="slide12-content">
                        <div className="slide12-text">
                            <h2>Пару слов обо мне и давайте начинать!</h2>
                            <p>
                                Меня зовут Сергей Морозов, и я много лет
                                набирался опыта работы в инвестиционных
                                компаниях на различных должностях. Сегодня я
                                знаю внутреннюю работу брокерского бизнеса,
                                чтобы помочь вам зарабатывать деньги на рынке.
                            </p>
                            <div className="buttons">
                                <a
                                    href={CONST.TG_ADMIN}
                                    target="_blank"
                                    rel="noreferrer"
                                >
                                    <button className="button">
                                        АДМИНИСТРАТОР ИГОРЬ
                                    </button>
                                </a>
                                <a
                                    href={CONST.TG_CHANNEL}
                                    target="_blank"
                                    rel="noreferrer"
                                >
                                    <button className="button">Telegram</button>
                                </a>
                                <a
                                    href={CONST.YT_CHANNEL}
                                    target="_blank"
                                    rel="noreferrer"
                                >
                                    <button className="button">YouTube</button>
                                </a>{" "}
                                <a
                                    href={"https://t.me/Nasdaq_Gad_bot"}
                                    target="_blank"
                                    rel="noreferrer"
                                >
                                    <button className="button">
                                        Telegram Bot
                                    </button>
                                </a>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </>
    );
}

Slide12.propTypes = {
    demo: PropTypes.bool,
    language: PropTypes.string.isRequired
};

export default Slide12;
